@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

:root {
  --primary-color: #e45858;
  --secondary-color: #6246ea;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  /* color: var(--primary-color); */
}

header {
  background: #e45858;
  padding: 10px;
}

header h2 {
  font-size: 40px !important;
  color: white !important;
  max-width: 1200px;
  margin: 0 auto;
}

h3 {
  padding-bottom: 10px;
  border-bottom: 1px solid #8d77f84f;
}

.content {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 100px;
}

/* .themeMode.dark {
  background: rgb(102, 102, 102);
  transition: all 0.2s ease-in-out;
  color: white !important;
} */

.section-header {
  display: grid;
  grid-template-columns: 0.55fr 0.2fr;
}

.section-header .toggleBtn {
  background: #fffffe;
  color: black;
  border: none;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.section-header .toggleBtn:hover {
  background: rgb(75, 74, 74);
  color: white;
}

.profile {
  background: rgb(230, 230, 230);
  padding: 20px;
  padding-top: 0.5px;
  padding-bottom: 30px !important;
  border-radius: 20px;
}
.footer {
  /* position: fixed; */
  left: 0;
  bottom: 0;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;
  /* height: 75px; */
  background-color: #f36969;
  color: white;
  text-align: center;
}
