@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
:root {
  --primary-color: #e45858;
  --secondary-color: #6246ea;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  /* color: var(--primary-color); */
}

header {
  background: #e45858;
  padding: 10px;
}

header h2 {
  font-size: 40px !important;
  color: white !important;
  max-width: 1200px;
  margin: 0 auto;
}

h3 {
  padding-bottom: 10px;
  border-bottom: 1px solid #8d77f84f;
}

.content {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 100px;
  gap: 100px;
}

/* .themeMode.dark {
  background: rgb(102, 102, 102);
  transition: all 0.2s ease-in-out;
  color: white !important;
} */

.section-header {
  display: grid;
  grid-template-columns: 0.55fr 0.2fr;
}

.section-header .toggleBtn {
  background: #fffffe;
  color: black;
  border: none;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.section-header .toggleBtn:hover {
  background: rgb(75, 74, 74);
  color: white;
}

.profile {
  background: rgb(230, 230, 230);
  padding: 20px;
  padding-top: 0.5px;
  padding-bottom: 30px !important;
  border-radius: 20px;
}
.footer {
  /* position: fixed; */
  left: 0;
  bottom: 0;
  width: 100%;
  padding-top: 25px;
  padding-bottom: 25px;
  /* height: 75px; */
  background-color: #f36969;
  color: white;
  text-align: center;
}

/* basics styles */
.skeleton {
  background: #ddd;
  margin: 10px 0;
  border-radius: 4px;
}

.skeleton.text {
  width: 100%;
  height: 12px;
}

.skeleton.title {
  width: 50%;
  height: 20px;
  margin-bottom: 15px;
}

.skeleton.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.skeleton.thumbnail {
  width: 100px;
  height: 100px;
}

.skeleton-wrapper {
  margin: 20px auto;
  padding: 10px 15px;
  border-radius: 4px;
  position: relative;
}

/* skeleton profile */
.skeleton-profile {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 30px;
  gap: 30px;
  align-items: center;
}

/* light and dark mode  themes */
.skeleton-wrapper.light {
  background: #f2f2f2;
  transition: all 0.1s ease-in-out;
}

.skeleton-wrapper.dark {
  background: #444;
  transition: all 0.1s ease-in-out;
}

.skeleton-wrapper.dark .skeleton {
  background: #777;
}

/* shimmer animation effect */
.shimmer-wrapper {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  -webkit-animation: loading 2.5s infinite;
          animation: loading 2.5s infinite;
  overflow: hidden;
}

.shimmer {
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  -webkit-transform: skewX(-20deg);
          transform: skewX(-20deg);
  box-shadow: 0 0 30px 30px rgba(255, 255, 255, 0.05);
}

.dark .shimmer {
  background: rgba(255, 255, 255, 0.05);
}

/* animation */
@-webkit-keyframes loading {
  0% {
    -webkit-transform: translateX(-150%);
            transform: translateX(-150%);
  }
  50% {
    -webkit-transform: translateX(-60%);
            transform: translateX(-60%);
  }
  100% {
    -webkit-transform: translateX(150%);
            transform: translateX(150%);
  }
}
@keyframes loading {
  0% {
    -webkit-transform: translateX(-150%);
            transform: translateX(-150%);
  }
  50% {
    -webkit-transform: translateX(-60%);
            transform: translateX(-60%);
  }
  100% {
    -webkit-transform: translateX(150%);
            transform: translateX(150%);
  }
}

